import React, { useState, useEffect, useCallback } from 'react';
import { getUrl, list, uploadData } from 'aws-amplify/storage'; // Use 'list' for listing files
import { Image, View, Button, Card, Flex, Text } from '@aws-amplify/ui-react';
import { useNavigate } from 'react-router-dom';
import '@aws-amplify/ui-react/styles.css';
import './styles.css';

const ProfilePage = ({ user, onProfileImageChange }) => {
  const [imageUrl, setImageUrl] = useState(null);
  const [mediaFiles, setMediaFiles] = useState([]);
  const [file, setFile] = useState(null);
  const [loading, setLoading] = useState(false); // Loading state for file operations
  const [fetchingMedia, setFetchingMedia] = useState(false); // Loading state for media fetch
  const [uploadError, setUploadError] = useState(''); // Error state for uploads
  const navigate = useNavigate();

  const defaultImagePath = 'default-pictures/user.PNG';

  const fetchProfileImage = useCallback(async () => {
    try {
      const result = await getUrl({
        path: `profile-pictures/${user.username}`,
        options: { validateObjectExistence: true, expiresIn: 900 }
      });
      setImageUrl(result.url); // Ensure you get the URL from the result object
      onProfileImageChange(result.url);
    } catch (error) {
      const result = await getUrl({
        path: defaultImagePath,
        options: { validateObjectExistence: true, expiresIn: 900 }
      });
      setImageUrl(result.url); // Ensure you get the URL from the result object
      onProfileImageChange(result.url);
    }
  }, [user.username, onProfileImageChange, defaultImagePath]);

  // Fetch the list of media files from S3, ensure fetch only runs on mount
  const fetchMediaFiles = useCallback(async () => {
  
    try {
      const response = await list(`public/media-files/${user.username}/`);
      const mediaList = response?.items || []; // Access items if available, otherwise use an empty array
      if (Array.isArray(mediaList)) {
        const mediaUrls = await Promise.all(mediaList.map(async (file) => {
          const result = await getUrl({
            path: file.key, // Use file.key to get the URL
            options: { expiresIn: 900 }
          });
          return result.url; // Return the URL from the result object
        }));
        setMediaFiles(mediaUrls);
      }
    } catch (error) {
      console.error("Error fetching media files: ", error);
    } finally {
      setFetchingMedia(false); // Fetching complete
    }
  }, [user.username]);

  useEffect(() => {
    fetchProfileImage();
    fetchMediaFiles(); // Fetch media files only once on mount
  }, [fetchProfileImage, fetchMediaFiles]); // Ensure this runs only once on mount

  const toggleOptions = () => {
    navigate('/manage-account');
  };

  // Handle file selection
  const handleFileChange = (e) => {
    setFile(e.target.files[0]);
    setUploadError(''); // Clear error when selecting a new file
  };

  // Upload the selected file to S3
  const handleUpload = async () => {
    if (file) {
      setLoading(true);
      try {
        const filePath = `public/media-files/${user.username}/${file.name}`;
        await uploadData({
          path: filePath,
          data: file,
          options: { contentType: file.type }
        });
        const result = await getUrl({
          path: filePath,
          options: { expiresIn: 900 }
        });
        setMediaFiles((prev) => [...prev, result.url]); // Add uploaded file to media list
        setFile(null); // Reset file input
      } catch (error) {
        setUploadError("Error uploading file. Please try again.");
      } finally {
        setLoading(false);
      }
    }
  };

  return (
    <View>
      <div className='page-container'>
        <View margin="3rem 0" alignItems="center" justifyContent="center">
          {imageUrl ? (
            <>
              <Image
                src={imageUrl}
                alt="Profile Picture"
                style={{ width: 150, height: 150, borderRadius: '50%', cursor: 'pointer', transition: 'transform 0.3s' }}
                onClick={toggleOptions}
                onMouseEnter={(e) => (e.target.style.transform = 'scale(1.1)')}
                onMouseLeave={(e) => (e.target.style.transform = 'scale(1)')}
              />
              <h2 style={{ marginTop: '1rem' }}>{user.username}</h2>
            </>
          ) : (
            <p>No profile picture uploaded</p>
          )}
        </View>

        {/* File upload section */}
        <View margin="1rem 0" alignItems="center" justifyContent="center">
          <input type="file" onChange={handleFileChange} />
          <Button onClick={handleUpload} disabled={!file || loading}>
            {loading ? 'Uploading...' : 'Upload'}
          </Button>
          {uploadError && <Text style={{ color: 'red' }}>{uploadError}</Text>}
        </View>

        {/* Display uploaded media as a grid of cards */}
        <Flex justifyContent="center" wrap="wrap">
          {fetchingMedia ? (
            <p>Loading media files...</p>
          ) : mediaFiles.length > 0 ? (
            mediaFiles.map((fileUrl, index) => (
              <Card key={index} margin="1rem" style={{ width: '200px', height: '200px' }}>
                <Image
                  src={fileUrl} // Ensure this URL is being passed correctly
                  alt={`Media ${index}`}
                  style={{ width: '100%', height: '100%' }}
                />
              </Card>
            ))
          ) : (
            <p>No media uploaded yet.</p>
          )}
        </Flex>
      </div>
    </View>
  );
};

export default ProfilePage;
